import React, { Component } from 'react';

import RenderRiddle from './render-riddle';
import CodeInput from './code-input';
import { textData } from './data'

import './app.css';

class App extends Component {
  textData = this.textData;
  startId = 1;

  state= {
    data: [
      this.createElement('684125'),
      this.createElement('928681'),
      this.createElement('431228'),
      this.createElement('168511'),
    ],
    currentText: textData[2]
  }

  newRender = (code) => {
    const { data } = this.state;


    const newState = data.find((item) => item.code === code)
    if(newState) {
      this.setState({
        currentText: newState.text
      })
    }else {
      this.setState({
        currentText: 'wrong code'
      })
    }
  }

  createElement(code) {
    return {
      code,
      text: textData[this.startId - 1],
      id: this.startId++
    }
  }

  render() {
    const { currentText } = this.state;

    return (
    <div className="App">
    <CodeInput onRenderRiddle={this.newRender}/>
    <RenderRiddle text={currentText}/>
    </div>
    );
  }
}

export default App;
